/* Tables */

.table {
  margin-bottom: 0;
  color: white;
  thead {
    th {
      border-top: 0;
      border-bottom-width: 1px;
      font-weight: 500;
      font-size: 1rem;
      i {
        margin-left: 0.325rem;
      }
    }
  }
  th,
  td {
    vertical-align: middle;
    line-height: 1;
    white-space: nowrap;
    padding: $table-cell-padding;
  }
  td {
    font-size: $default-font-size;
    img {
      width: 36px;
      height: 36px;
      border-radius: 100%;
    }
    .badge {
      margin-bottom: 0;
    }
  }
  &.table-borderless {
    border: none;
    tr,
    td,
    th {
      border: none;
    }
  }
  &.table-bordered {
    border-top: 1px solid $border-color;
  }
}

.table-hover > tbody > tr:hover {
  color: $text-muted;
}

.select__all {
  padding: 0 5px;
  input {
    margin-top: 0;
  }
}

.table-scrollable {
  height: 80vh;
  overflow: scroll;
}

@media (max-width: 992px) {
  .table-scrollable {
    height: auto;
    overflow: auto;
  }
}
