////////// SIDEBAR ////////
$sidebar-width-lg: 237px;
$sidebar-width-mini: 185px;
$sidebar-logo-width-lg: 100px;
$sidebar-width-icon: 70px;

$sidebar-bg: $content-bg;
$sidebar-menu-color: #fff;
$sidebar-menu-active-bg: #3a3f51;
$sidebar-menu-active-color: $primary;
$sidebar-menu-hover-bg: #3a3f51;
$sidebar-menu-hover-color: #d0cfcf;
$sidebar-submenu-color: $sidebar-menu-color;
$sidebar-submenu-hover-bg: initial;
$sidebar-submenu-hover-color: #bebdc2;
$sidebar-submenu-active-color: #ffffff;
$sidebar-menu-icon-color: inherit;
$sidebar-menu-arrow-color: $sidebar-menu-color;

$sidebar-menu-font-size: 0.875rem;
$sidebar-menu-padding-left: 1rem;
$sidebar-menu-padding-right: 1.937rem;
$sidebar-menu-padding-top: 0.75rem;
$sidebar-menu-padding-bottom: 0.75rem;

$sidebar-submenu-padding: 0.25rem 0 0.6rem 3.07rem;
$sidebar-submenu-font-size: 0.875rem;
$sidebar-submenu-item-padding: 0.7rem 1rem;

$sidebar-icon-font-size: 1rem;
$sidebar-arrow-font-size: 0.625rem;

$sidebar-profile-bg: transparent;
$sidebar-profile-padding: 0 1.625rem 2.25rem 1.188rem;

$sidebar-mini-menu-padding: 0.8125rem 1rem 0.8125rem 1rem;

$sidebar-icon-only-menu-padding: 0.5rem 1.625rem 0.5rem 1.188rem;
$sidebar-icon-only-submenu-padding: 0 0 0.6rem 1.5rem;

$rtl-sidebar-submenu-padding: 0 4.5rem 0 0;

$icon-only-collapse-width: 190px;
$icon-only-menu-bg: $sidebar-bg;

///////// SIDEBAR ////////

